import React, { useRef } from "react";
import "./loginPage.scss";
import { Link } from "react-router-dom";
import Kivi from "../../assets/icons/kivi.svg";
import axios from "axios";

const URL = "https://reqres.in/api";

export const Login = ({ setToken }) => {
  const emailRef = useRef(null);
  const passwordRef = useRef(null);

  const handleSubmit = (e) => {
    e.preventDefault();

    const info = {
      email: emailRef.current.value,
      password: passwordRef.current.value,
    };

    console.log(info);

    axios
      .post(`${URL}/login`, info)
      .then((res) => {
        if (res.status === 200) {
          localStorage.setItem("token", res?.data?.token);
          setToken(res?.data?.token)
        }
      })
      .catch((err) => {
        alert(err)
      });
  };

  return (
    <>
      <header>
        <div className="container">
          <div className="login-dis">
            <div className="login-wrap">
              <h4 className="log">Ваша регистрация завершена!</h4>
              <h6 className="log-text">
                Вы успешно зарегистрированы на сайте киви и ваше пароль
                отправлен в виде смс на ваше телефонный номер
              </h6>
              <form onSubmit={handleSubmit}>
                <label className="label1">
                  <h5>Введите email или номер телефона</h5>
                  <input
                    ref={emailRef}
                    type="text"
                    placeholder="99891 167 27 23"
                  />
                </label>
                <label className="label1">
                  <h5>Пароль</h5>
                  <input
                    ref={passwordRef}
                    type="password"
                    placeholder="***********"
                  />
                </label>
                <button type="submit" className="enter">
                  Войти
                </button>
              </form>
              <div className="dis-input">
                <input type="text" />
                <h6>или</h6>
                <input type="text" />
              </div>
              <h6 className="h6-TEXT">Восстоновить ваше пароль</h6>
            </div>
            <div className="bg-img">
              <div className="color">
                <img className="kivi" src={Kivi} alt="" />
                <p className="tag-br">
                  Откройте для себя <br /> целую плошадку <br /> для продаже
                </p>
                <button className="kak">Как это делать?</button>
              </div>
            </div>
          </div>
        </div>
      </header>
    </>
  );
};
