import "./header.scss";

import Logo from "../../assets/icons/logo.svg";
import LineIcon from "../../assets/icons/Line-icon.svg";
import navPerson from "../../assets/nav-prson.png";
import kiviIconNav from "../../assets/icons/nav-svg-kivi.svg";
import groupSvg from "../../assets/icons/Group-svg.svg";

import { Link } from "react-router-dom";

export const Header = ({ setToken }) => {
  const handleClick = () => {
    localStorage.clear("token");
    setToken("");
  };

  return (
    <>
      <nav>
        <div className="container">
          <div className="nav-display">
            <Link className="nonedecoration nav-logo" to="/">
              <img src={kiviIconNav} alt="" />
              <h5>Продай, найди, купи все что пожелаешь…</h5>
            </Link>
            <ul className="ul1">
              <li className="Объявления">
                <Link className="Сообщения2" to="/error">
                  Объявления
                </Link>
              </li>
              <li className="Сообщения">
                <Link className="Сообщения2" to="/error">
                  Сообщения
                </Link>
              </li>
              <li className="Сообщения">
                <Link to="/error" className="Сообщения2">
                  Платежи и счёт
                </Link>
              </li>
              <li className="Сообщения">
                <Link className="Сообщения2" to="/error">
                  Настройки
                </Link>
              </li>
              <li className="Сообщения">
                <Link to="/error" className="Сообщения2">
                  Мой бизнес
                </Link>
              </li>
            </ul>
            <ul className="ul2">
              <li>Рус </li>
              <li> | </li>
              <li> O’z</li>
            </ul>
            <div className="parent">
              <div className="img-dis">
                <h4>Темиров</h4>
                <img src={navPerson} alt="" />
              </div>
              <div className="logout">
                <Link className="toProfile" to="/profile">
                  Profile
                </Link>
                <button onClick={handleClick} className="red__button">
                  Logout
                </button>
              </div>
            </div>
          </div>
          <div className="displayDiv-btn">
            <div className="search-delo">
              <div className="display-div">
                <h6>Любая категория</h6>
                <img src={groupSvg} alt="" />
              </div>
              <div className="h4-div">
                <h4 className="display-div-h4">Что будем искать ?</h4>
              </div>
              <div className="display-div2">
                <h6>Любая категория</h6>
                <img src={groupSvg} alt="" />
              </div>
              <button className="search-btn">Найти</button>
            </div>
            <div className="div-btn-plus">
              <button className="BTN">+</button>
              <h5>Добавить обьявления</h5>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};
