import "./home.scss";

import React from "react";

import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";

import { Chapter } from "../../Components/Chapter/Chapter";


import downLogo from "../../assets/icons/Group.svg"


import Airplane from "../../assets/headerIcons/Airplane.svg";
import Store from "../../assets/headerIcons/Store.svg";
import Terminal from "../../assets/headerIcons/Terminal.svg";
import BriefCase from "../../assets/headerIcons/Briefcase.svg";
import Gifts from "../../assets/headerIcons/Gifts.svg";
import Chalange from "../../assets/headerIcons/Challenge.svg";
import Building from "../../assets/headerIcons/In Maintenance.svg";
import Xobbi from "../../assets/headerIcons/Gaming.svg";
import Business from "../../assets/headerIcons/Man.svg";
import { useEffect, useState } from "react";
import axios from "axios";

import Kreslo from "../../assets/images/Kreslo.png";
import Gucci from "../../assets/images/gucci.png";
// import HalfTool from "../../assets/images/half__tool.png";
import Nout from "../../assets/images/nout.png";

// const [isLoading, setIsLoading] = useState(false);
// const [isError, setIsError] = useState(false);

const BaseUrl = "https://fakestoreapi.com/products/";
export const Home = ({ children }) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);

  // const [currentSlide, setCurrentSlide] = useState(0);
  useEffect(() => {
    // Function to fetch data using Axios
    const fetchData = async () => {
      try {
        const response = await axios.get(BaseUrl);
        setData(response.data);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }
  console.log(data);
  const carouselSettings = {
    dots: false, // Hide the default navigation dots
    infinite: true,
    speed: 500,
    slidesToShow: 3, // Number of visible items in the barrel
    slidesToScroll: 1,
    centerMode: true, // Enable center mode
    variableWidth: true, // Allow variable width for slides
    autoplay: true, // Enable autoplay
    autoplaySpeed: 3000, // Set the interval between rotations (in milliseconds)
  };
  const firstData = [
    {
      id: 1,
      title: "Fjallraven - Foldsack No. 1 Backpack, Fits 15 Laptops",
      price: 109.95,
      description:
        "Your perfect pack for everyday use and walks in the forest. Stash your laptop (up to 15 inches) in the padded sleeve, your everyday",
      category: "men's clothing",
      image: "https://fakestoreapi.com/img/81fPKd-2AYL._AC_SL1500_.jpg",
      rating: { rate: 3.9, count: 120 },
    },
    {
      id: 2,
      title: "Mens Casual Premium Slim Fit T-Shirts ",
      price: 22.3,
      description:
        "Slim-fitting style, contrast raglan long sleeve, three-button henley placket, light weight & soft fabric for breathable and comfortable wearing. And Solid stitched shirts with round neck made for durability and a great fit for casual fashion wear and diehard baseball fans. The Henley style round neckline includes a three-button placket.",
      category: "men's clothing",
      image:
        "https://fakestoreapi.com/img/71-3HjGNDUL._AC_SY879._SX._UX._SY._UY_.jpg",
      rating: { rate: 4.1, count: 259 },
    },
    {
      id: 3,
      title: "Mens Cotton Jacket",
      price: 55.99,
      description:
        "great outerwear jackets for Spring/Autumn/Winter, suitable for many occasions, such as working, hiking, camping, mountain/rock climbing, cycling, traveling or other outdoors. Good gift choice for you or your family member. A warm hearted love to Father, husband or son in this thanksgiving or Christmas Day.",
      category: "men's clothing",
      image: "https://fakestoreapi.com/img/71li-ujtlUL._AC_UX679_.jpg",
      rating: { rate: 4.7, count: 500 },
    },
    {
      id: 4,
      title: "Mens Casual Slim Fit",
      price: 15.99,
      description:
        "The color could be slightly different between on the screen and in practice. / Please note that body builds vary by person, therefore, detailed size information should be reviewed below on the product description.",
      category: "men's clothing",
      image: "https://fakestoreapi.com/img/71YXzeOuslL._AC_UY879_.jpg",
      rating: { rate: 2.1, count: 430 },
    },
  ];

  return (
    <>
      <header className="header">
        <div className="container header__ct header__chapters">
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={Airplane} BgImg="purple" title="Автомобили" />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={Store} BgImg="cyan" title=" Недвижимость" />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={Terminal} BgImg="red" title=" Электроника" />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={BriefCase} BgImg="yellow" title=" Работа" />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={Gifts} BgImg="humanColor" title="Личные вещи" />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={Chalange} BgImg="cyan" title=" Дом и Сад" />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter
              ImgUrl={Building}
              BgImg="purple"
              title="Стройка и ремонт"
            />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={Xobbi} BgImg="humanColor" title=" Хобби и Спорт" />
          </Link>
          <Link to="/error" className="header__icon--link">
            <Chapter ImgUrl={Business} BgImg="red" title=" Бизнес и услуги" />
          </Link>
        </div>
      </header>

      <section className="sectionAds">
        <div className="container sectionAds__ct">
          <div className="sectionAds__text">
            <h2 className="sectionAds__text--title">Новые объявления</h2>
            <h2 className="sectionAds__text--secondTitle">
              Лучщие предложение
            </h2>
          </div>
          <div className="sectionAds__posts">
            {data.map((watched) => (
              <div className="section__card" key={watched?.id}>
                <Link to={`/product/${watched.id}`} className="nonedecoration">
                  <div className="section__card--image">
                    <img
                      className="section__card--img"
                      src={watched.image}
                      alt={watched.title}
                    />
                  </div>

                  <div className="section__card--text">
                    <h3 className="section__card--title">{watched.title}</h3>
                    <p className="section__card--pretitle">Вчера 22:55</p>
                    <h4 className="section__card--price">{watched.price}$</h4>
                  </div>
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>

      <main className="main">
        <div className="main__ct">
          <div className="main__cards">
            <div className="main__card">
              <Link to="/error">
                <img src={Nout} alt="" />
              </Link>
            </div>
            <div className="main__card">
              <Link to="/error">
                <img src={Gucci} alt="" />
              </Link>
            </div>
            <div className="main__card">
              <Link to="/error">
                <img src={Kreslo} alt="" />
              </Link>
            </div>
          </div>
        </div>
      </main>
      <div className="unique">
        <div className="container special__ct">
          <div className="sectionAds__text">
            <h2 className="sectionAds__text--title">Просмотренные</h2>
            <h2 className="sectionAds__text--secondTitle">Выбор редакции</h2>
            <h2 className="sectionAds__text--secondTitle">Сниженные цены</h2>
          </div>

          <div className="sectionAds__cards">
            <div className="cards">
              {firstData.map((product) => (
                <div className="section__card" key={product.id}>
                  <Link
                    to={`/product/${product.id}`}
                    className="nonedecoration"
                  >
                    <div className="section__card--image">
                      <img
                        className="section__card--img"
                        src={product.image}
                        alt={product.title}
                      />
                    </div>

                    <div className="section__card--text">
                      <h3 className="section__card--title">{product.title}</h3>
                      <p className="section__card--pretitle">Вчера 22:55</p>
                      <h4 className="section__card--price">{product.price}$</h4>
                    </div>
                  </Link>
                </div>
              ))}
            </div>
            <button className="details">Покозать еще
              <img className="image" src={downLogo} alt="" />
            </button>
          </div>
        </div>
      </div>
    </>
  );
};
