import "./chapter.scss"

export const Chapter = ({ BgImg, ImgUrl, title }) => {
  return (
    <div className="card">
      <div className={BgImg}>
        <img src={ImgUrl} alt={title} />
      </div>
      <div className="card__text">
        <h3 className="card__text--title">{title}</h3>
      </div>
    </div>
  );
};
